body {
  margin: 0;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale
}

code {
  font-family: source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace
}

.appHeading {
  font-size: 1.4em;
  width: 100%;
  min-height: 40px;
  position: fixed;
  z-Index: 1000;
  background-color: #000;
  color: #fff;
  padding: 6px;
  opacity: .85;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center
}

.countries {
  padding-top: 60px
}

.country {
  text-align: center;
  padding-top: 0
}

.name {
  font-size: 1.2em;
  font-weight: 700;
  margin-bottom: 6px;
  display: flex;
  justify-content: center;
  align-items: center
}

.badge {
  margin-left: 10px;
  display: inline-block;
  min-width: 16px;
  height: 16px;
  padding: 3px;
  border-radius: 4px;
  font-size: 12px;
  text-align: center;
  background: #1779ba;
  color: #fff
}

.medals {
  text-transform: capitalize
}

.medals button {
  margin-left: 10px;
  width: 25px
}

